import { useState, Fragment } from 'react';
import About from '~components/About';

import styles from './styles.scss';

const Body = () => {
  const [page, setPage] = useState(0);

  return (
    <Fragment>
      <About page={page} setPage={setPage} classNames={page === 0 && styles.selected} />
      <About page={page} setPage={setPage} classNames={page === 1 && styles.selected} />
      <About page={page} setPage={setPage} classNames={page === 2 && styles.selected} />
    </Fragment>
  );
};

export default Body;
